.articles_container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-left: 44px;
  margin-top: 50px;
  margin-bottom: 80px;
  position: relative;
}
.articles_container input {
  position: fixed;
  top: 10px;
  padding-left: 60px;
  height: 44px;
  max-width: 640px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.06);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  border-radius: 12px;
  border: 0;
  z-index: 1;
}

.loading {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
  font-weight: 800;
  font-size: 26px;
}

.profile,
.admin {
  position: fixed;
  right: 20px;
  top: 70px;
  padding: 5px 10px;
  border-radius: 12px;
  border: 0;
  background-color: rgba(0, 0, 0, 0.06);
  transition: all 0.2s linear;
  font-size: 14px;
  font-weight: 600;
}
.profile:hover,
.admin:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.08);
}
.profile:active,
.admin:active {
  transform: scale(0.9);
}

.admin {
  top: 105px;
}

.popular,
.reset {
  position: fixed;
  left: 20px;
  top: 70px;
  padding: 5px 10px;
  border-radius: 12px;
  border: 0;
  background-color: rgba(0, 0, 0, 0.06);
  transition: all 0.2s linear;
  font-weight: 600;
}
.popular:hover,
.reset:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.08);
  transform: scale(1.05);
}

.reset {
  left: 180px;
}

.output {
  position: fixed;
  top: 70px;
  left: 278px;
  background-color: white;
  z-index: 1;
  width: 702px;
  padding: 10px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 12px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.2);
}
.output div {
  transition: all 0.2s linear;
  padding: 5px 10px;
  border-radius: 12px;
}
.output div:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.06);
}