.small_header {
  position: fixed;
  top: 0;
  left: 20%;
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding-top: 10px;
  background-color: #f3f3f2;
  z-index: 2;
}

.back {
  width: 20px;
  height: 20px;
  background: url("../../../icons/back.svg") center center no-repeat;
  transition: all 0.2s linear;
}
.back:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.about_author {
  display: flex;
  gap: 10px;
  align-items: center;
}

.author_img {
  width: 60px;
  height: 60px;
  background: url("../../../icons/no_image.svg") center center no-repeat;
}

.author_name {
  font-size: 22px;
  font-weight: 600;
}

.publish {
  border-radius: 12px;
  padding: 5px 20px;
  background-color: coral;
  font-size: 16px;
  font-weight: 400;
  color: white;
  border: 0;
  transition: all 0.2s linear;
}
.publish:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.publish:active {
  transform: scale(0.95);
}

.main {
  margin: 100px 0;
}
.main form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-file {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.input-file input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}
.input-file span {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  vertical-align: middle;
  color: rgb(255, 255, 255);
  text-align: center;
  border-radius: 12px;
  background-color: coral;
  line-height: 22px;
  height: 40px;
  padding: 10px 20px;
  box-sizing: border-box;
  border: none;
  margin: 0;
  transition: background-color 0.2s;
}

.title_input {
  background-color: #f3f3f2;
  border: 0;
  border-bottom: 1px solid black;
  min-width: 500px;
  min-height: 70px;
  max-width: 700px;
  max-height: 300px;
  outline: none;
  font-size: 52px;
  font-weight: 600;
}

.text_input {
  background-color: #f3f3f2;
  border: 0;
  border-bottom: 1px solid black;
  min-width: 500px;
  min-height: 100px;
  max-width: 700px;
  max-height: 500px;
  outline: none;
  font-weight: 300;
  font-size: 26px;
}

.user_pic {
  width: 70px;
  height: 70px;
}

.user-no-img {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: coral;
  border-radius: 100%;
  font-size: 34px;
  font-weight: 600;
  color: white;
  width: 60px;
  height: 60px;
}

.aside-error {
  display: none;
  position: fixed;
  width: 200px;
  left: 20px;
  top: 30%;
  color: red;
  font-weight: 600;
  font-size: 22px;
  text-align: center;
  border-right: 1px solid red;
  transition: all 0.3s linear;
}