.comments_container {
  margin-top: 80px;
}
.comments_container .comments-title {
  font-weight: 800;
  font-size: 24px;
}
.comments_container .comments-title span {
  font-weight: 400;
  font-size: 20px;
  color: grey;
}

.comment-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0 40px 0;
}
.comment-form .comment-avatar {
  width: 60px;
  height: 60px;
  background: url("../../../icons/no_image.svg") center center no-repeat;
}
.comment-form .comment-form_text {
  width: 80%;
  max-width: 80%;
  min-width: 80%;
  min-height: 30px;
  max-height: 200px;
  border-radius: 12px;
  padding: 5px 10px;
  font-size: 18px;
}
.comment-form .comment-send {
  width: 40px;
  height: 40px;
  background: url("../../../icons/send.svg") center center no-repeat;
  transition: all 0.2s linear;
  border-radius: 12px;
}
.comment-form .comment-send:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.06);
}
.comment-form .comment-send:active {
  transform: scale(0.9);
}

.comment-card {
  margin: 10px 0;
  border-bottom: 1px solid black;
  padding-bottom: 10px;
}
.comment-card .comment-top-side {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  position: relative;
}
.comment-card .comment-top-side .comment-img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin: 0;
}
.comment-card .comment-top-side .comment-ava {
  background-color: coral;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  font-weight: 600;
  font-size: 20px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.comment-card .comment-top-side .comment_username {
  font-weight: 800;
  font-size: 22px;
}
.comment-card .comment-top-side .comment-date {
  font-size: 14px;
  color: grey;
}

#error3 {
  margin-top: -20px;
  text-align: center;
}

.delete {
  position: absolute;
  right: 0;
  width: 30px;
  height: 30px;
  background: url("../../../icons/delete.svg") center center no-repeat;
  cursor: pointer;
  transition: all 0.2s linear;
}
.delete:hover {
  transform: scale(1.1);
}